/* åˆ†éš”çº¿ä¸Žå¯¼èˆªå®¹å™¨ */
#post-hr {
  border: none;
  border-top: 1px dashed #CC6D96;
//   margin: 10px 0;
}

.post-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  color: #555;
  margin: 5px 0;
  padding: 5px 0;
  text-align: center;
  gap: 10px; /* é—´è·æ›´æ¸…æ™° */
}

.post-nav a {
  color: #CC6D96;
  text-decoration: none;
  padding: 8px 12px;
  border-radius: 6px;
  transition: all 0.3s ease;
  flex: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* å·¦ä¾§é“¾æŽ¥ï¼šå‰ä¸€ç¯‡ */
.post-nav > :first-child {
  text-align: left;
  color: #CC6D96;
}

/* ä¸­é—´é“¾æŽ¥ï¼šéšä¾¿é€›é€› */
.post-nav > :nth-child(2) {
  text-align: center;
  color: #CC6D96;
  font-weight: 500;
//   background-color: #fef8f0;
//   border: 1px dashed #e67e22;
}

