.main-content p:has(> img.post-image:nth-child(2)),
.post-content p:has(> img.post-image:nth-child(2)) {
  column-count: 2;
  column-gap: 8px;
  margin: 6px 0;
}

.main-content p:has(> img.post-image:nth-child(3)),
.post-content p:has(> img.post-image:nth-child(3)) { 
  column-count: 3; }

.main-content p:has(> img.post-image:nth-child(4)),
.post-content p:has(> img.post-image:nth-child(4)) { 
  column-count: 4; }


.main-content p:has(> img.post-image:nth-child(5)),
.post-content p:has(> img.post-image:nth-child(5)) { 
  column-count: 5; }

.main-content p:has(> img.post-image:nth-child(6)),
.post-content p:has(> img.post-image:nth-child(6)) { 
  column-count: 4; }

.main-content p:has(> img.post-image:nth-child(2)) img,
.post-content p:has(> img.post-image:nth-child(2)) img { 
  display: inherit; }

.main-content p:has(> img.post-image:nth-child(6)) img,
.post-content p:has(> img.post-image:nth-child(6)) img { 
  margin-bottom: 8px; }