/* styles.css */
.album-container {
    max-width: 640px;
    margin: 0 auto;
    padding: 20px;
}

.album-header {
    background-color: #f4e8d7;
    padding: 20px;
    border-radius: 5px;
    margin-bottom: 20px;
}

.album-header h1 {
    font-size: 24px;
    margin-top: 0;
}

.announcement {
    margin-top: 20px;
}

.announcement ul {
    list-style-type: none;
    padding: 0;
}

.announcement li {
    margin: 5px 0;
}

.image-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); /* è°ƒæ•´æœ€å°å®½åº¦ */
    gap: 10px;
}


/* styles.css */
.image-item {
    text-align: center; /* ç¡®ä¿æ ‡é¢˜èƒ½å¤Ÿå±…ä¸­å¯¹é½ */
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    overflow: hidden;
    padding: 3px 0;
}

.image-item img {
    width: auto;
    height: 129px;
    display: block;
}

.flower {
    -webkit-mask-image: url(/imgs/cs5sCEo.png); 
    mask-image: url(/imgs/cs5sCEo.png); 
    
    -webkit-mask-size: 100%; 
    mask-size: 100%; 
    
    -webkit-mask-repeat: no-repeat; 
    mask-repeat: no-repeat; 
    
    -webkit-mask-position: center; 
    mask-position: center; 
}

