.pagination {
    text-align: center;
    margin-top: 20px;
}

.pagination ul {
    list-style-type: none;
    padding: 0;
}

.pagination li {
    display: inline-block;
    margin: 0 5px;
}

.pagination a {
    display: inline-block;
    padding: 5px 10px;
    background-color: #f4e8d7;
    color: #333;
    text-decoration: none;
    border-radius: 3px;
}

.image-grid > p:empty,
.image-grid > p:first-child:empty,
.image-grid > p:last-child:empty {
    display: none;
}

