.post-list {
    padding-inline-start: 0 !important;
    padding-left: 0 !important;
    margin: 0 0 0.8rem 0;
    // border-left: 3px dashed #b39eb5;
    list-style: none;
}

/* ç¡®ä¿ li æ²¡æœ‰å·¦ä¾§é—´è· */
.post-list .post-list-item {
    margin-left: 0 !important;
    // padding-left: .5rem;
    // border-bottom: 1px solid #f0f0f0;
    border-bottom: 2px dashed #b39eb5;
    padding-bottom: 0.2rem;
    margin-bottom: 0.2rem;
}

.post-list-date {
    color: #666;
    font-size: 0.9em;
    margin-right: 0.5rem;
}

.post-list-link {
    color: #333;
    text-decoration: none;
    transition: color 0.2s;
}

.post-list-link:hover {
    color: #0066cc;
}

.post-list-more {
    margin-left: 0;
    padding-left: 1rem;
    text-align: right;
    margin-top: 1rem;
}

.post-list-more-link {
    color: #0066cc;
    font-size: 0.9em;
    text-decoration: underline;
    // font-style: italic;
}