:root {
  --color-bg-code: #2e3440;
  --color-accent: #f0c4e7;
  --color-secondary: #cab8f1;
  --color-tertiary: #7acaff;
  --color-text: #fdf6e3;
  --color-light: #756a85;
  --border-radius: 6px;
}

code:not(pre > code) {
//   background: var(--color-bg-code);
//   color: var(--color-text);
  padding: 0.2em 0.4em;
  border-radius: 6px;
  font-size: 0.85em;
  border: 1px solid #e8d7ff;
}

pre {
  background: var(--color-bg-code) !important; /* æ·»åŠ  !important ç¡®ä¿è¦†ç›– */
  border-radius: var(--border-radius);
  padding: 1.5em 1.2em 1.2em;
  overflow: auto;
  white-space: pre-wrap;
  word-wrap: break-word;
  max-height: 300px;
  // border: 2px solid #e8d7ff;
  position: relative;
  margin: .5em 0;
}

pre code {
//   background: transparent !important;
  font-size: 0.85em;
  font-family: "Segoe UI", "Roboto", "Noto Sans", "LXGW WenKai Mono TC", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
  color: var(--color-text) !important; /* æ·»åŠ  !important ç¡®ä¿è¦†ç›– */
  display: block;
  padding: 0;
}

