.hljs-keyword { color: #ff6bd6; font-weight: 500; }
.hljs-string { color: #7acaff; }
.hljs-function { color: #b28dff; }
.hljs-comment { color: #c0a1e0; font-style: italic; }
.hljs-number { color: #ff9d6c; }

.copy-code-button {
//   color: var(--color-light);
//   background: linear-gradient(to right, var(--color-accent), var(--color-secondary));
  border: none;
  border-radius: 20px;
  padding: 6px 12px;
  font-size: 0.8rem;
  position: absolute;
  z-index: 1;
  right: 15px;
  top: 15px;
  margin: 0;
  transition: all 0.3s ease;
  opacity: 0.85;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}

.copy-code-button:hover {
  opacity: 1;
  transform: translateY(-2px);
  cursor: pointer;
}

.copy-code-button:active {
  transform: translateY(0);
}

.copy-code-button > svg {
  fill: var(--color-light);
  height: 14px;
  width: 14px;
}

.highlight {
  position: relative;
  margin: .5em 0;
}

.language-label {
  position: absolute;
  left: 15px;
  top: -10px;
//   background: linear-gradient(to right, var(--color-secondary), var(--color-tertiary));
//   color: white;
  padding: 3px 12px;
  border-radius: 15px;
  font-size: 0.75rem;
  font-weight: 500;
  z-index: 2;
}

code.has-jax {
  -webkit-font-smoothing: antialiased;
//   background: inherit !important;
  border: none !important;
  font-size: 100%;
}

pre code.hljs {
  display: block;
  overflow: auto;
  padding: 0;
  white-space: pre-wrap;
  max-height: 350px;
}