.pagination {
  text-align: center;
  margin: 30px 0;
  font-size: 14px;
}

.pagination-inner {
  display: inline-block;
  background: #FEE3FD;
  border-radius: 10px;
  border: 2px solid #FBFEEA;
  padding: 4px;
  letter-spacing: 1px;
}

.pagination-link {
  display: inline-block;
  padding: 4px 8px;
  margin: 0 2px;
  color: #9D6AA6;
  text-decoration: none;
  border-radius: 5px;
}

.pagination-link:hover:not(.disabled) {
  background: #fff;
  color: #CC6D96;
}

.pagination-link.current {
  background: #CC6D96;
  color: white;
  font-weight: bold;
}

.pagination-link.disabled {
  color: #ccc;
  cursor: not-allowed;
}